ReactDOM.render(
    <>

    <h1 className="heading_style">Pranav's Personal favourite Animes</h1>

    {/* fat arrow function and map method */}
    {Data.map((value, index) => {
        return (
            <Card 
            imgsrc={value.imgscr} 
            title={value.title} 
            sname={value.seriesName} 
            info={value.info} 
            link={value.links} 
            />
        );
    })}

    <footer>&copy; 2021 | Pranav Dipika Chandrakant Patil | ReactJS</footer>

    </>, document.getElementById('root') 
);